form {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    align-items: center;
    flex-direction: column;
    background-color: #f5f5f5;
    padding: 30px;
    border-radius: 8px;
}

label {
    color: #233553;
    font-family: 'PressStart', sans-serif;
    font-size: 12px;
    margin-bottom: 30px;
}

textarea {
    width: 100%;
    margin-bottom: 30px;
    font-family: 'PressStart', sans-serif;
    padding: 15px;
    font-size: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.TodoForm-button {
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 10px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
    font-family: 'PressStart', sans-serif;
}

.TodoForm-button--cancel {
    background-color: #6d6d6d;
}

.TodoForm-button--add {
    background-color: #233553;
}

