.TodoItem-loading {
    list-style-type: none;
    margin: 20px auto;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    min-width: 250px;
    max-width: 450px;
    min-height: 34px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: aliceblue;
    position: relative;

    animation: loadingAnimation 1s infinite;    
}

@keyframes loadingAnimation {
    0% {
        opacity: 0.5;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0.5;
    }
}