li {
    list-style-type: none;
    margin: 20px auto;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    min-width: 250px;
    max-width: 450px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: aliceblue;
    position: relative;
}

.TodoItem__p {
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-family: 'PressStart', sans-serif;
    font-size: 10px;
}

.Icon {
    cursor: pointer;
}

.Icon-check, .Icon-uncheck {
    font-weight: bolder;
    font-size: 24px;
    padding: 3px;
    margin-right: 10px;
}

.Icon-delete {
    font-size: 28px;
    position: absolute;
    top: 0px;
    right: 0px;
    font-weight: bolder;
}

.Icon-delete:hover {
    color: red;
}

.TodoItem__p--complete {
    text-decoration: line-through;
    color: grey;
}