.CreateTodoButton{
    /* Create floating button */
    position: fixed;
    bottom: 16px;
    right: 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    font-size: 30px;
    cursor: pointer;
    z-index: 1;
    transition: background-color 0.5s, transform 0.5s;
}

.CreateTodoButton.active{
    background-color: #35a7dc;
    transform: rotate(45deg);
}